<template>
  <div class="community-container">
    <!-- Barra de navegación superior -->
    <nav class="nav-container">
      <div class="logo">
        <router-link to="/" class="logo-link">🎮 GameHub</router-link>
      </div>
      <ul>
        <li>
          <router-link to="/juego"><i class="icon">🎮</i> Jugar</router-link>
        </li>
        <li class="dropdown" data-testid="comunidad-item">
          <router-link to="/comunidad" class="dropdown-toggle">
            <i class="icon">👥</i> Comunidad
          </router-link>
          <ul class="dropdown-menu">
            <li><router-link to="/capturas">Capturas</router-link></li>
            <li><router-link to="/comunidad/guias">Guias</router-link></li>
            <li><router-link to="/retransmisiones">Retransmisiones</router-link></li>
            <li><router-link to="/videos">Videos</router-link></li>
          </ul>
        </li>
        <li>
          <router-link :to="isAuthenticated ? '/perfil' : '/login'">
            <i class="icon">{{ isAuthenticated ? '👤' : '🔑' }}</i>
            {{ isAuthenticated ? 'Perfil' : 'Login' }}
          </router-link>
        </li>
      </ul>
    </nav>

    <!-- Contenedor principal -->
    <div class="main-content">
      <!-- Contenido dinámico basado en la ruta -->
      <div class="content-section">
        <!-- Mostrar guías si la ruta es /comunidad/guias -->
        <div v-if="$route.path === '/comunidad/guias'" class="guides-content">
          <!-- Header Section -->
          <div class="guides-header">
            <h2>Guías</h2>
            <p>Explora guías creadas por la comunidad para mejorar tu experiencia de juego.</p>
            <router-link v-if="isAuthenticated" to="/comunidad/crear-guia" class="create-guide-btn">
              <i class="icon">✍️</i> Crear Nueva Guía
            </router-link>
          </div>

          <!-- Lista de guías o placeholder -->
          <div v-if="loadingGuides" class="loading-message">
            Cargando guías...
          </div>
          <div v-else-if="errorMessage" class="error-message">
            {{ errorMessage }}
          </div>
          <div v-else-if="guides.length === 0" class="no-guides-placeholder">
            <div class="placeholder-content">
              <i class="placeholder-icon">📖</i>
              <p>No hay guías disponibles todavía.</p>
              <p>¡Sé el primero en compartir tus conocimientos con la comunidad!</p>
            </div>
          </div>
          <div v-else class="guides-list">
            <div 
              class="guide-card" 
              v-for="guide in guides" 
              :key="guide.id" 
              @click="viewGuide(guide)"
            >
              <div class="guide-card-image">
                <img v-if="guide.image" :src="guide.image" alt="Portada de la guía" @error="handleImageError" />
                <div v-else class="no-image-placeholder">Sin imagen</div>
              </div>
              <div class="guide-card-content">
                <h3>Título: {{ guide.title }}</h3>
                <p class="guide-description">Descripción: {{ guide.description || 'Sin descripción' }}</p>
                <p class="guide-meta"><strong>Categoría:</strong> {{ guide.category || 'Sin categoría' }}</p>
                <p class="guide-meta"><strong>Creado por:</strong> {{ guide.user?.name || 'Anónimo' }}</p>
              </div>
              <div class="guide-actions" @click.stop>
                <button v-if="guide.user?.name === user?.name" class="action-btn edit-btn" @click="editGuide(guide)">Editar</button>
                <button v-if="guide.user?.name === user?.name" class="action-btn delete-btn" @click="deleteGuide(guide.id)">Eliminar</button>
              </div>
            </div>
          </div>
        </div>

        <!-- Contenido genérico para /comunidad -->
        <div v-else>
          <h2>Comunidad</h2>
          <p>Bienvenido a la comunidad de GameHub. Explora guías, artwork, retransmisiones y videos.</p>
        </div>
      </div>
    </div>

    <!-- Footer -->
    <footer class="footer-section">
      <div class="footer-content">
        <p>© 2025 GameHub - Todos los derechos reservados</p>
        <div class="social-links">
          <a href="#" class="social-icon">🐦</a>
          <a href="#" class="social-icon">📘</a>
          <a href="#" class="social-icon">📷</a>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import axios from 'axios';
import { isAuthenticated } from '@/api/auth';

export default {
  name: 'CommunityView',
  data() {
    return {
      isAuthenticated: localStorage.getItem('auth_token') !== null,
      guides: [],
      loadingGuides: false,
      errorMessage: '',
      successMessage: '',
      user: null,
    };
  },
  created() {
    window.addEventListener('storage', this.updateAuthStatus);
    this.checkAuthStatus();
    this.fetchUserData();

    // Cargar guías si estamos en la ruta /comunidad/guias
    if (this.$route.path === '/comunidad/guias') {
      this.fetchGuides();
    }
  },
  watch: {
    // Observar cambios en la ruta y cargar guías si es necesario
    '$route'(to) {
      if (to.path === '/comunidad/guias') {
        this.fetchGuides();
      }
    },
  },
  beforeUnmount() {
    window.removeEventListener('storage', this.updateAuthStatus);
  },
  methods: {
    async checkAuthStatus() {
      this.isAuthenticated = await isAuthenticated();
    },
    updateAuthStatus() {
      this.isAuthenticated = localStorage.getItem('auth_token') !== null;
    },
    async fetchUserData() {
      try {
        const token = localStorage.getItem('auth_token');
        if (!token) return;

        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        const response = await axios.get('/api/user');
        this.user = {
          name: response.data.name,
          email: response.data.email,
        };
      } catch (error) {
        this.errorMessage = error.response ? error.response.data.message : error.message;
      }
    },
    async fetchGuides() {
      this.loadingGuides = true;
      this.errorMessage = '';
      try {
        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        await axios.get('sanctum/csrf-cookie');
        const response = await axios.get('/api/guides/all');
        this.guides = response.data;
      } catch (error) {
        const errorMsg = error.response
          ? `${error.response.status}: ${error.response.data.message || error.response.statusText}`
          : error.message;
        this.errorMessage = errorMsg;
        this.guides = [];
      } finally {
        this.loadingGuides = false;
      }
    },
    handleImageError(event) {
      event.target.style.display = 'none';
      const placeholder = document.createElement('div');
      placeholder.className = 'no-image-placeholder';
      placeholder.textContent = 'Sin imagen';
      event.target.parentNode.appendChild(placeholder);
    },
    editGuide(guide) {
      this.$router.push(`/comunidad/crear-guia?editGuide=${guide.id}`);
    },
    async deleteGuide(guideId) {
      if (confirm('¿Estás seguro de que quieres eliminar esta guía?')) {
        try {
          const response = await axios.delete(`/api/guides/${guideId}`);
          this.successMessage = response.data.message;
          await this.fetchGuides();
        } catch (error) {
          this.errorMessage = error.response ? error.response.data.message : error.message;
        }
      }
    },
    viewGuide(guide) {
      this.$router.push(`/comunidad/guia/${guide.id}`);
    },
  },
};
</script>

<style scoped>
