  color: #e63946;
  font-weight: bold;
  transform: scale(1.05);
}

/* Estilos para el dropdown (solo para Comunidad) */
.nav-container ul li.dropdown {
  position: relative;
}

.nav-container ul li.dropdown .dropdown-toggle {
