  background: #ff6f61;
  color: white;
}

/* Contenido principal */
.main-content {
  width: 85%;
  max-width: 900px;
  margin: 30px 0;
  flex-grow: 1;
}

/* Contenido */
.content-section {
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}

