  color: #666;
  font-size: 1.2em;
}

/* Header for Guides Section */
.guides-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}

.guides-header h2 {
  margin-top: 0;
