}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 900px;
  margin: 0 auto;
}

.social-links {
  display: flex;
  gap: 15px;
}

.social-icon {
  text-decoration: none;
  color: white;
