<template>
  <div class="login-container">
    <nav class="nav-container">
      <div class="logo">
        <router-link to="/" class="logo-link">🎮 GameHub</router-link>
      </div>
      <ul>
        <li><router-link to="/"><i class="icon">🏠</i> Volver al Inicio</router-link></li>
        <li><router-link to="/register"><i class="icon">✍️</i> Registrarse</router-link></li>
        <li><router-link to="/about"><i class="icon">ℹ️</i> Acerca de</router-link></li>
      </ul>
    </nav>

    <div class="login-content">
      <h2 class="animated-title">Iniciar Sesión</h2>
      <form @submit.prevent="handleLogin" class="login-form">
        <div class="form-group">
          <label for="email">Email:</label>
          <input type="email" id="email" v-model="email" autocomplete="username" required />
        </div>
        <div class="form-group">
          <label for="password">Contraseña:</label>
          <input type="password" id="password" v-model="password" autocomplete="current-password" required />
        </div>
        <button type="submit">Iniciar Sesión</button>
        <p v-if="successMessage" class="success-message">{{ successMessage }}</p>
        <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
      </form>
      <div id="g_id_onload"
           :data-client_id="googleClientId"
           data-callback="handleGoogleSignIn"
           data-auto_prompt="false">
      </div>
      <div class="g_id_signin"
           data-type="standard"
           data-size="large"
           data-theme="outline"
           data-text="sign_in_with"
           data-shape="rectangular"
           data-logo_alignment="left">
      </div>
    </div>

    <footer class="footer-section">
      <div class="footer-content">
        <p>© 2025 GameHub - Todos los derechos reservados</p>
        <div class="social-links">
          <a href="#" class="social-icon">🐦</a>
          <a href="#" class="social-icon">📘</a>
          <a href="#" class="social-icon">📷</a>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      email: '',
      password: '',
      successMessage: '',
      errorMessage: '',
      googleClientId: '916302553196-267v6rqu5djak1t94f9ub0eeqv9oh8rn.apps.googleusercontent.com',
    };
  },
  async created() {
    // Configurar Axios con base URL
    axios.defaults.baseURL = 'http://localhost:8000';
    axios.defaults.withCredentials = true;

    // Obtener token CSRF
    try {
      await axios.get('/sanctum/csrf-cookie');
      console.log('Token CSRF inicializado correctamente');
    } catch (error) {
      console.error('Error al obtener el token CSRF:', error.message);
      this.errorMessage = 'No se pudo conectar con el servidor.';
    }

    // Cargar el script de Google Identity Services
    const script = document.createElement('script');
    script.src = 'https://accounts.google.com/gsi/client';
    script.async = true;
    script.defer = true;
    script.onload = () => {
      console.log('Google Identity Services cargado');
    };
    script.onerror = () => {
      console.error('Error al cargar Google Identity Services');
      this.errorMessage = 'No se pudo cargar Google Sign-In.';
    };
    document.body.appendChild(script);

    // Declarar la función de callback globalmente
    window.handleGoogleSignIn = this.handleGoogleSignIn;
  },
  methods: {
    async handleLogin() {
      try {
        const response = await axios.post('/api/login', {
          email: this.email,
          password: this.password,
        });
        console.log('Respuesta del servidor:', response.data);
        if (response.data.token) {
          localStorage.setItem('auth_token', response.data.token);
          this.successMessage = 'Inicio de sesión exitoso.';
          this.errorMessage = '';
          setTimeout(() => this.$router.push('/perfil'), 1000);
        }
      } catch (error) {
        console.error('Error en login:', error.response ? error.response.data : error.message);
        this.errorMessage = error.response?.data?.message || 'Error en inicio de sesión.';
        this.successMessage = '';
      }
    },
    async handleGoogleSignIn(response) {
      try {
        console.log('Iniciando sesión con Google...', response);
        const idToken = response.credential;
        console.log('ID Token:', idToken);

        const serverResponse = await axios.post('/api/google-login', { id_token: idToken });
        console.log('Respuesta del servidor:', serverResponse.data);
        if (serverResponse.data.token) {
          localStorage.setItem('auth_token', serverResponse.data.token);
          this.successMessage = 'Inicio de sesión con Google exitoso.';
          this.errorMessage = '';
          setTimeout(() => this.$router.push('/perfil'), 1000);
        }
      } catch (error) {
        console.error('Error al iniciar sesión con Google:', error);
        this.errorMessage = 'Error al iniciar sesión con Google.';
        this.successMessage = '';
      }
    },
  },
};
</script>

<style scoped>
/* Tus estilos actuales */
.login-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1e1e2f 0%, #2a2a3f 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Arial', sans-serif;
}

.nav-container {
  width: 100%;
  padding: 15px 20px;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-link {
  text-decoration: none;
  font-size: 1.5em;
  font-weight: bold;
  color: #ff6f61;
  transition: color 0.3s;
}

.logo-link:hover {
  color: #e63946;
}

.nav-container ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.nav-container ul li {
  margin: 0 20px;
}

.nav-container ul li a {
  text-decoration: none;
  color: #ff6f61;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  transition: color 0.3s, transform 0.3s;
}

.nav-container ul li a .icon {
  margin-right: 8px;
}

.nav-container ul li a:hover {
  color: #e63946;
  transform: scale(1.1);
}

.login-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 60px;
  width: 85%;
  max-width: 500px;
  flex-grow: 1;
}

.animated-title {
  font-size: 2.5em;
  margin-bottom: 30px;
  color: #ff6f61;
  text-align: center;
  animation: fadeIn 1.5s ease-in-out;
}

@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(-20px); }
  100% { opacity: 1; transform: translateY(0); }
}

.login-form {
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
  width: 100%;
  text-align: center;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

.form-group label {
  display: block;
  font-size: 1.1em;
  color: #1e1e2f;
  margin-bottom: 5px;
}

.login-form input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1em;
  background-color: #f9f9f9;
  box-sizing: border-box;
  transition: border-color 0.3s;
}

.login-form input:focus {
  border-color: #ff6f61;
  outline: none;
}

.login-form button {
  width: 100%;
  padding: 12px;
  margin: 20px 0 0;
  background: linear-gradient(90deg, #ff6f61, #ff9f1c);
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 1.2em;
  cursor: pointer;
  transition: transform 0.3s, background 0.3s;
}

.login-form button:hover {
  background: linear-gradient(90deg, #e63946, #ff9f1c);
  transform: scale(1.05);
}

.g_id_signin {
  margin-top: 20px;
}

.success-message {
  color: #2a9d8f;
  margin-top: 15px;
  font-weight: bold;
}

.error-message {
  color: #e63946;
  margin-top: 15px;
  font-weight: bold;
}

.footer-section {
  background: linear-gradient(90deg, #2a9d8f, #1e1e2f);
  color: white;
  text-align: center;
  padding: 20px;
  width: 100%;
  margin-top: auto;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 900px;
  margin: 0 auto;
}

.social-links {
  display: flex;
  gap: 15px;
}

.social-icon {
  text-decoration: none;
  color: white;
  font-size: 1.5em;
  transition: color 0.3s;
}

.social-icon:hover {
  color: #ff6f61;
}
</style>