<template>
  <div class="profile-container">
    <!-- Barra de navegación superior -->
    <nav class="nav-container">
      <div class="logo">
        <router-link to="/" class="logo-link">🎮 GameHub</router-link>
      </div>
      <ul>
        <li>
          <router-link to="/juego"><i class="icon">🎮</i> Jugar</router-link>
        </li>
        <li class="dropdown" data-testid="comunidad-item">
          <router-link to="/comunidad" class="dropdown-toggle">
            <i class="icon">👥</i> Comunidad
          </router-link>
          <ul class="dropdown-menu">
            <li><router-link to="/comunidad/guias">Guías</router-link></li>
            <li><router-link to="/artwork">Artwork</router-link></li>
            <li><router-link to="/retransmisiones">Retransmisiones</router-link></li>
            <li><router-link to="/videos">Videos</router-link></li>
          </ul>
        </li>
        <li>
          <router-link to="/perfil" class="active">
            <i class="icon">👤</i> Perfil
          </router-link>
        </li>
        <li v-if="isAuthenticated">
          <button @click="logout" class="logout-btn">
            <i class="icon">🚪</i> Logout
          </button>
        </li>
        <li v-else>
          <router-link to="/login">
            <i class="icon">🔑</i> Login
          </router-link>
        </li>
      </ul>
    </nav>

    <!-- User Header -->
    <div class="user-header">
      <div class="user-avatar">
        <i class="avatar-icon">👤</i>
      </div>
      <div class="user-info">
        <div class="user-details">
          <h1 class="username">{{ user?.name || 'Usuario' }}</h1>
        </div>
        <button class="edit-profile-btn" @click="editProfile">
          Editar Perfil
        </button>
      </div>
    </div>

    <!-- Main Content with Statistics -->
    <div class="profile-content">
      <!-- Main Content -->
      <div class="main-content">
        <!-- Navigation Tabs -->
        <div class="tabs">
          <button :class="{ 'active-tab': activeTab === 'characters' }" @click="activeTab = 'characters'">
            Personajes
          </button>
          <button :class="{ 'active-tab': activeTab === 'matches' }" @click="activeTab = 'matches'">
            Partidas
          </button>
          <button :class="{ 'active-tab': activeTab === 'achievements' }" @click="activeTab = 'achievements'">
            Logros
          </button>
          <button :class="{ 'active-tab': activeTab === 'stats' }" @click="activeTab = 'stats'">
            Estadísticas
          </button>
        </div>

        <!-- Content Sections -->
        <div class="content-section">
          <!-- My Guides Section -->
          <div class="guides-section">
            <h2>Mis Guías</h2>
            <div v-if="loadingGuides" class="loading-message">
              Cargando guías...
            </div>
            <div v-else-if="errorMessage" class="error-message">
              {{ errorMessage }}
            </div>
            <div v-else-if="userGuides.length === 0" class="no-guides-placeholder">
              <p>No has creado ninguna guía todavía.</p>
              <router-link to="/comunidad" class="create-guide-link">
                Crear una Guía
              </router-link>
            </div>
            <div v-else class="guides-grid">
              <div class="guide-card" v-for="guide in userGuides" :key="guide.id">
                <div class="guide-image">
                  <img :src="guide.image" alt="Portada de la guía" @error="handleImageError" />
                </div>
                <div class="guide-content">
                  <h3>Título: {{ guide.title }}</h3>
                  <p class="guide-description">Descripción: {{ truncateDescription(guide.description, 100) }}</p>
                  <p class="guide-meta"><strong>Categoría:</strong> {{ guide.category || 'Sin categoría' }}</p>
                </div>
                <div class="guide-actions">
                  <button class="action-btn view-btn" @click="viewGuide(guide)">Ver Guía</button>
                  <button class="action-btn edit-btn" @click="editGuide(guide)">Editar</button>
                  <button class="action-btn delete-btn" @click="deleteGuide(guide.id)">Eliminar</button>
                </div>
              </div>
            </div>
            <p v-if="successMessage" class="success-message">{{ successMessage }}</p>
          </div>

          <!-- Comments Section -->
          <div class="comments-section">
            <h2>Comentarios</h2>
            <p>Aquí se mostrarían los comentarios del usuario.</p>
          </div>
        </div>
      </div>

      <!-- Statistics Section -->
      <div class="statistics-section">
        <h3>Estadísticas</h3>
        <p class="status">{{ user?.status || 'En línea' }}</p>
        <div class="stats">
          <p><strong>Inventario:</strong> {{ user?.inventory || 0 }}</p>
          <p><strong>Capturas:</strong> {{ user?.screenshots || 0 }}</p>
          <p><strong>Videos:</strong> {{ user?.videos || 0 }}</p>
          <p><strong>Artículos del Workshop:</strong> {{ user?.workshopItems || 0 }}</p>
          <p><strong>Reseñas:</strong> {{ user?.reviews || 0 }}</p>
          <p><strong>Guías:</strong> {{ userGuides.length }}</p>
          <p><strong>Artwork:</strong> {{ user?.artwork || 0 }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { isAuthenticated } from '@/api/auth';

export default {
  name: 'ProfileView',
  data() {
    return {
      isAuthenticated: localStorage.getItem('auth_token') !== null,
      activeTab: 'characters',
      user: null,
      guides: [],
      userGuides: [],
      loadingGuides: false,
      errorMessage: '',
      successMessage: '',
    };
  },
  created() {
    window.addEventListener('storage', this.updateAuthStatus);
    this.checkAuthStatus();
    this.fetchUserData();
    this.fetchGuides();
  },
  beforeUnmount() {
    window.removeEventListener('storage', this.updateAuthStatus);
  },
  methods: {
    async checkAuthStatus() {
      this.isAuthenticated = await isAuthenticated();
      if (!this.isAuthenticated) {
        this.$router.push('/login');
      }
    },
    updateAuthStatus() {
      this.isAuthenticated = localStorage.getItem('auth_token') !== null;
    },
    async fetchUserData() {
      try {
        const token = localStorage.getItem('auth_token');
        if (!token) {
          return;
        }

        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        const response = await axios.get('/api/user');
        this.user = {
          name: response.data.name,
          email: response.data.email,
          location: response.data.location || 'Ubicación desconocida',
          level: response.data.level || 1,
          status: response.data.status || 'En línea',
          badges: response.data.badges || 0,
          games: response.data.games || 0,
          inventory: response.data.inventory || 0,
          screenshots: response.data.screenshots || 0,
          videos: response.data.videos || 0,
          workshopItems: response.data.workshopItems || 0,
          reviews: response.data.reviews || 0,
          artwork: response.data.artwork || 0,
        };
      } catch (error) {
        this.errorMessage = error.response ? error.response.data.message : error.message;
      }
    },
    async fetchGuides() {
      this.loadingGuides = true;
      this.errorMessage = '';
      try {
        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        await axios.get('sanctum/csrf-cookie');
        const token = localStorage.getItem('auth_token');
        const response = await axios.get('/api/guides', {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        this.userGuides = response.data;
      } catch (error) {
        const errorMsg = error.response
          ? `${error.response.status}: ${error.response.data.message || error.response.statusText}`
          : error.message;
        this.errorMessage = errorMsg;
        this.userGuides = [];
      } finally {
        this.loadingGuides = false;
      }
    },
    viewGuide(guide) {
      this.$router.push(`/comunidad/guia/${guide.id}`);
    },
    editGuide(guide) {
      this.$router.push(`/comunidad?editGuide=${guide.id}`);
    },
    async deleteGuide(guideId) {
      if (confirm('¿Estás seguro de que quieres eliminar esta guía?')) {
        try {
          axios.defaults.baseURL = 'http://localhost:8000';
          axios.defaults.withCredentials = true;
          await axios.get('sanctum/csrf-cookie');
          const token = localStorage.getItem('auth_token');
          const response = await axios.delete(`/api/guides/${guideId}`, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });
          this.successMessage = response.data.message || 'Guía eliminada con éxito';
          await this.fetchGuides();
        } catch (error) {
          this.errorMessage = error.response ? error.response.data.message : error.message;
        }
      }
    },
    handleImageError(event) {
      event.target.style.display = 'none';
      const placeholder = document.createElement('div');
      placeholder.className = 'no-image-placeholder';
      placeholder.textContent = 'Sin imagen';
      event.target.parentNode.appendChild(placeholder);
    },
    truncateDescription(description, maxLength) {
      if (!description) return 'Sin descripción';
      return description.length > maxLength
        ? description.substring(0, maxLength) + '...'
        : description;
    },
    editProfile() {
      this.$router.push('/perfil/editar');
    },
    async logout() {
      try {
        console.log('Starting logout process...');
        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        console.log('Fetching CSRF token...');
        await axios.get('sanctum/csrf-cookie');
        console.log('CSRF token fetched successfully.');
        const token = localStorage.getItem('auth_token');
        if (!token) {
          console.log('No auth token found, proceeding with client-side logout.');
          localStorage.removeItem('auth_token');
          this.isAuthenticated = false;
          this.$router.push('/login');
          return;
        }
        console.log('Sending logout request to /api/logout...');
        const response = await axios.post('/api/logout', {}, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        console.log('Logout response:', response.data);
        localStorage.removeItem('auth_token');
        this.isAuthenticated = false;
        console.log('Redirecting to /login...');
        this.$router.push('/login');
      } catch (error) {
        console.error('Error during logout:', error);
        if (error.response) {
          console.error('Response data:', error.response.data);
          console.error('Response status:', error.response.status);
          this.errorMessage = `Error al cerrar sesión: ${error.response.data.message || error.response.statusText}`;
        } else {
          this.errorMessage = 'Error al cerrar sesión. Por favor, intenta de nuevo.';
        }
        localStorage.removeItem('auth_token');
        this.isAuthenticated = false;
        this.$router.push('/login');
      }
    },
  },
};
</script>

<style scoped>
/* Prevent horizontal scrolling on the body */
body {
  overflow-x: hidden;
}

.profile-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1e1e2f 0%, #2a2a3f 100%);
  display: flex;
  flex-direction: column;
