  transform: scale(1.05);
}

/* Estilos para el dropdown (copiados de CommunityView.vue) */
.nav-container ul li.dropdown {
  position: relative;
}

.nav-container ul li.dropdown .dropdown-toggle {
  text-decoration: none;
  color: #ff6f61;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  transition: color 0.3s, transform 0.3s;
  cursor: pointer;
}

.nav-container ul li.dropdown .dropdown-toggle .icon {
  margin-right: 8px;
}

.nav-container ul li.dropdown .dropdown-toggle:hover {
  color: #e63946;
  transform: scale(1.1);
}

.nav-container ul li.dropdown .dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  min-width: 150px;
  z-index: 20;
  padding: 5px 0;
}

.nav-container ul li.dropdown:hover .dropdown-menu {
  display: block;
}

.nav-container ul li.dropdown .dropdown-menu li {
  display: block;
  margin: 0;
}

.nav-container ul li.dropdown .dropdown-menu li a {
  display: block;
  padding: 8px 15px;
  color: #ff6f61;
  text-decoration: none;
  font-size: 1em;
  transition: background 0.3s, color 0.3s;
}

.nav-container ul li.dropdown .dropdown-menu li a:hover {
  background: #ff6f61;
  color: white;
}

/* User Header */
.user-header {
  width: 100%;
  max-width: 1200px;
  margin: 20px auto;
  background: rgba(255, 255, 255, 0.95);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding-left: 20px;
  padding-right: 20px;
}

.user-avatar {
  margin-right: 20px;
}

.avatar-icon {
  font-size: 3em;
  color: #ff6f61;
}

.user-info {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user-details {
  text-align: center;
}

.username {
  margin: 0;
  color: #ff6f61;
  font-size: 2em;
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

.level-badge {
  background: #2a9d8f;
  color: white;
  padding: 5px 10px;
  border-radius: 15px;
  font-size: 0.9em;
}

.location,
.status {
  color: #666;
  font-size: 1.1em;
  margin: 5px 0;
}

.edit-profile-btn {
  background: linear-gradient(90deg, #ff6f61, #ff9f1c);
  color: white;
  padding: 8px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
  transition: transform 0.3s, background 0.3s;
}

.edit-profile-btn:hover {
  background: linear-gradient(90deg, #e63946, #ff9f1c);
  transform: scale(1.05);
}

/* Profile Content */
.profile-content {
  width: 100%;
  max-width: 1200px;
  margin: 20px auto;
  display: flex;
  gap: 20px;
  flex-direction: row-reverse;
  box-sizing: border-box;
  padding: 0 20px;
}

/* Statistics Section */
.statistics-section {
  width: 250px;
  background: rgba(255, 255, 255, 0.95);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  align-self: flex-start;
  flex-shrink: 0;
}

.statistics-section h3 {
  color: #ff6f61;
  font-size: 1.5em;
  margin-top: 0;
}

.statistics-section .status {
  color: #2a9d8f;
  font-size: 1.1em;
  margin: 10px 0;
}

.stats p {
  color: #666;
  font-size: 1em;
  margin: 5px 0;
}

.stats p strong {
  color: #ff6f61;
}

/* Main Content */
.main-content {
  flex-grow: 1;
  box-sizing: border-box;
}

/* Navigation Tabs */
.tabs {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
}

.tabs button {
  background: linear-gradient(90deg, #2a9d8f, #1e1e2f);
  color: #fff;
  border: none;
  padding: 10px 15px;
  margin-right: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
  transition: transform 0.3s, background 0.3s;
}

.tabs button.active-tab {
  background: linear-gradient(90deg, #1e1e2f, #2a9d8f);
  transform: scale(1.05);
  font-weight: bold;
}

.tabs button:hover {
  background: linear-gradient(90deg, #1e1e2f, #2a9d8f);
  transform: scale(1.05);
}

/* Content Sections */
.content-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.guides-section,
.comments-section {
  background: rgba(255, 255, 255, 0.95);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
}

.guides-section h2,
.comments-section h2 {
  margin-top: 0;
  color: #ff6f61;
  font-size: 1.8em;
  animation: fadeIn 1.5s ease-in-out;
}

/* Placeholder for No Guides */
.no-guides-placeholder {
  text-align: center;
  padding: 15px;
}

.no-guides-placeholder p {
  color: #666;
  font-size: 1.1em;
  margin: 5px 0;
}

.create-guide-link {
  display: inline-block;
  margin-top: 10px;
  background: linear-gradient(90deg, #ff6f61, #ff9f1c);
  color: white;
  padding: 8px 15px;
  border-radius: 5px;
  text-decoration: none;
  transition: transform 0.3s, background 0.3s;
}

.create-guide-link:hover {
  background: linear-gradient(90deg, #e63946, #ff9f1c);
  transform: scale(1.05);
}

/* Guide Grid */
.guides-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 15px;
}

.guide-card {
  background: #f9f9f9;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  box-sizing: border-box;
}

.guide-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
}

.guide-image {
  width: 100%;
  height: 150px;
  overflow: hidden;
}

.guide-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.no-image-placeholder {
  width: 100%;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666;
  font-size: 1em;
  background: #f0f0f0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.guide-content {
  padding: 15px;
}

.guide-content h3 {
  margin: 0 0 8px;
  color: #ff6f61;
  font-size: 1.3em;
}

.guide-meta {
  color: #2a9d8f;
