  font-size: 0.95em;
  margin: 0;
  line-height: 1.4;
}

.guide-actions {
  display: flex;
  gap: 8px;
  padding: 10px 15px;
  background: #e9e9e9;
  border-top: 1px solid #ddd;
}

.action-btn {
  flex: 1;
  padding: 8px;
  border: none;
  border-radius: 5px;
