}

.view-btn {
  background: linear-gradient(90deg, #4285f4, #34a853);
}

.view-btn:hover {
  background: linear-gradient(90deg, #357abd, #2d8b43);
}

.edit-btn {
  background: linear-gradient(90deg, #ff6f61, #ff9f1c);
}

.edit-btn:hover {
  background: linear-gradient(90deg, #e63946, #ff9f1c);
}

.delete-btn {
  background: linear-gradient(90deg, #e63946, #ff9f1c);
}

.delete-btn:hover {
  background: linear-gradient(90deg, #d62828, #ff9f1c);
}

.loading-message {
  text-align: center;
  color: #ff6f61;
  font-size: 1.1em;
}

.error-message {
  text-align: center;
  color: #e63946;
  font-size: 1.1em;
  font-weight: bold;
}

.success-message {
  text-align: center;
  color: #2a9d8f;
  font-size: 1.1em;
  font-weight: bold;
  margin-top: 10px;
}

/* Comments Section */
.comments-section p {
  color: #666;
  font-size: 1.1em;
}

/* Animations */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Media Queries */
@media (max-width: 768px) {
  .user-header {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 10px;
    padding-right: 10px;
  }

  .user-avatar {
    margin-bottom: 15px;
  }

  .user-info {
    flex-direction: column;
    align-items: center;
  }

  .edit-profile-btn {
    margin-top: 15px;
  }

  .profile-content {
    flex-direction: column;
    padding: 0 10px;
  }

  .statistics-section {
    width: 100%;
    align-self: center;
  }

  .tabs {
    flex-wrap: wrap;
  }

  .tabs button {
    margin-bottom: 10px;
  }

  .guides-grid {
    grid-template-columns: 1fr;
  }

  .nav-container ul {
    padding: 0 10px;
  }

  .nav-container ul li {
    margin: 0 10px;
  }
}
</style>