<template>
    <div id="game-container">
      <!-- Aquí va el contenedor del juego -->
      <iframe src="http://localhost:3000" frameborder="0" width="100%" height="100%"></iframe>
    </div>
  </template>
  
  <script>
  export default {
    name: 'RpgGame',
    data() {
      return {
        gameData: null, // Aquí guardamos el progreso del juego
      };
    },
    methods: {
      // Función para guardar el juego en la base de datos
      async saveGameToBackend(gameData) {
        try {
          const response = await fetch('http://localhost/api/save-game', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${this.$store.state.userToken}`,  // Asumiendo que tienes un token de autenticación
            },
            body: JSON.stringify({ game_data: gameData })
          });
  
          const data = await response.json();
          console.log('Juego guardado en el backend:', data);
        } catch (error) {
          console.error('Error al guardar la partida:', error);
        }
      },
  
      // Función para cargar el juego desde la base de datos
      async loadGameFromBackend() {
        try {
          const response = await fetch('http://localhost/api/load-game', {
            method: 'GET',
            headers: {
              'Authorization': `Bearer ${this.$store.state.userToken}`,  // Asegúrate de usar el token de autenticación del usuario
            }
          });
  
          const data = await response.json();
          if (data.game_data) {
            this.gameData = data.game_data;  // Asigna los datos cargados al juego
            console.log('Juego cargado desde el backend:', this.gameData);
          } else {
            console.log('No hay juego guardado');
          }
        } catch (error) {
          console.error('Error al cargar la partida:', error);
        }
      }
    }
  };
  </script>
  
  <style scoped>
  /* Aquí van los estilos del contenedor del juego */
  #game-container {
    width: 100%;
    height: 100vh; /* Ocupa toda la altura de la pantalla */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  iframe {
    width: 100%;
    height: 100%;
    border: none;
  }
  </style>  