<template>
  <div class="guide-detail-container">
    <!-- Barra de navegación superior -->
    <nav class="nav-container">
      <div class="logo">
        <router-link to="/" class="logo-link">🎮 GameHub</router-link>
      </div>
      <ul>
        <li data-testid="volver-item">
          <router-link to="/comunidad/guias">
            <i class="icon">📖</i> Volver a las guías
          </router-link>
        </li>
        <li class="dropdown" data-testid="comunidad-item">
          <router-link to="/comunidad" class="dropdown-toggle">
            <i class="icon">👥</i> Comunidad
          </router-link>
          <ul class="dropdown-menu">
            <li><router-link to="/capturas">Capturas</router-link></li>
            <li><router-link to="/artwork">Artwork</router-link></li>
            <li><router-link to="/retransmisiones">Retransmisiones</router-link></li>
            <li><router-link to="/videos">Videos</router-link></li>
          </ul>
        </li>
        <li>
          <router-link :to="isAuthenticated ? '/perfil' : '/login'">
            <i class="icon">{{ isAuthenticated ? '👤' : '🔑' }}</i>
            {{ isAuthenticated ? 'Perfil' : 'Login' }}
          </router-link>
        </li>
      </ul>
    </nav>

    <!-- Main Content -->
    <div class="guide-content-wrapper">
      <div v-if="loading" class="loading-message">Cargando guía...</div>
      <div v-else-if="errorMessage" class="error-message">{{ errorMessage }}</div>
      <div v-else-if="guide" class="guide-content">
        <div class="guide-header">
          <div class="guide-image-container">
            <img v-if="guide.image" :src="guide.image" alt="Portada de la guía" @error="handleImageError" />
            <div v-else class="no-image-placeholder">Sin imagen</div>
          </div>
          <div class="guide-title-container">
            <div class="title-and-actions">
              <h1 class="guide-title">{{ guide.title }}</h1>
              <!-- Botones de Editar y Eliminar -->
              <div class="header-actions" v-if="isAuthenticated && guide.createdBy === user?.name">
                <button class="action-btn edit-btn" @click="editGuide">
                  <span class="icon">✏️</span> Editar
                </button>
                <button class="action-btn delete-btn" @click="deleteGuide">
                  <span class="icon">🗑️</span> Eliminar
                </button>
              </div>
            </div>
            <div class="guide-rating">
              <span class="stars">{{ displayStars(averageRating) }}</span>
              <span class="rating-text">
                {{ averageRating > 0 ? `${averageRating.toFixed(1)}/5 (${ratings.length} valoraciones)` : 'No hay suficientes valoraciones' }}
              </span>
            </div>
            <p class="guide-author">Por {{ guide.createdBy || 'Anónimo' }}</p>
          </div>
        </div>

        <div class="guide-actions">
          <button class="action-btn like-btn">
            <span class="icon">👍</span> Me gusta
          </button>
          <button class="action-btn dislike-btn">
            <span class="icon">👎</span> No me gusta
          </button>
          <button class="action-btn award-btn">
            <span class="icon">🏆</span> Premiar
          </button>
          <button class="action-btn favorite-btn" @click="toggleFavorite">
            <span class="icon">{{ isFavorited ? '🌟' : '☆' }}</span>
            {{ isFavorited ? 'Quitar de Favoritos' : 'Agregar a Favoritos' }}
          </button>
          <button class="action-btn share-btn" @click="shareGuide">
            <span class="icon">🔗</span> Compartir
          </button>
        </div>

        <!-- Contenido principal y metadatos -->
        <div class="guide-main-content">
          <div class="guide-body">
            <p class="guide-content-text">{{ guide.content }}</p>
          </div>
          <div class="guide-meta">
            <h3>Detalles</h3>
            <div class="meta-item">
              <span class="meta-label">Creado por:</span>
              <span class="meta-value">{{ guide.createdBy || 'Anónimo' }}</span>
            </div>
            <div class="meta-item">
              <span class="meta-label">Categoría:</span>
              <span class="meta-value">{{ guide.category || 'Sin categoría' }}</span>
            </div>
            <div class="meta-item">
              <span class="meta-label">Idioma:</span>
              <span class="meta-value">Español</span>
            </div>
            <div class="meta-item">
              <span class="meta-label">Publicado:</span>
              <span class="meta-value">{{ formatDate(guide.created_at) }}</span>
            </div>
            <div class="meta-item">
              <span class="meta-label">Actualizado:</span>
              <span class="meta-value">{{ formatDate(guide.updated_at || guide.created_at) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { isAuthenticated } from '@/api/auth';

export default {
  data() {
    return {
      guide: null,
      loading: false,
      errorMessage: '',
      isAuthenticated: localStorage.getItem('auth_token') !== null,
      isFavorited: false,
      comments: [],
      newComment: '',
      ratings: [],
      userRating: 0,
      userReview: '',
      averageRating: 0,
      user: null, // Añadimos el usuario autenticado
    };
  },
  created() {
    window.addEventListener('storage', this.updateAuthStatus);
    this.checkAuthStatus();
    this.fetchGuide();
    this.loadComments();
    this.loadRatings();
    this.fetchUserData(); // Cargar datos del usuario autenticado
  },
  mounted() {
    const dropdownToggles = document.querySelectorAll('.dropdown-toggle');
    dropdownToggles.forEach(toggle => {
      toggle.addEventListener('click', this.toggleDropdown);
    });
    document.addEventListener('click', this.closeDropdown);
  },
  beforeUnmount() {
    window.removeEventListener('storage', this.updateAuthStatus);
    const dropdownToggles = document.querySelectorAll('.dropdown-toggle');
    dropdownToggles.forEach(toggle => {
      toggle.removeEventListener('click', this.toggleDropdown);
    });
    document.removeEventListener('click', this.closeDropdown);
  },
  methods: {
    async checkAuthStatus() {
      this.isAuthenticated = await isAuthenticated();
      console.log('Estado de autenticación:', this.isAuthenticated);
    },
    updateAuthStatus() {
      this.isAuthenticated = localStorage.getItem('auth_token') !== null;
      console.log('Estado de autenticación actualizado:', this.isAuthenticated);
    },
    async fetchUserData() {
      try {
        const token = localStorage.getItem('auth_token');
        if (!token) {
          console.log('No hay token de autenticación, usuario no autenticado');
          return;
        }

        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        const response = await axios.get('/api/user');
        this.user = {
          name: response.data.name,
          email: response.data.email,
        };
        console.log('Datos del usuario cargados:', this.user);
      } catch (error) {
        this.errorMessage = error.response ? error.response.data.message : error.message;
        console.error('Error al cargar datos del usuario:', error);
      }
    },
    async fetchGuide() {
      this.loading = true;
      this.errorMessage = '';
      try {
        const guideId = this.$route.params.id;
        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        await axios.get('sanctum/csrf-cookie');
        const response = await axios.get(`/api/guides/${guideId}`);
        this.guide = response.data;
        console.log('Datos de la guía:', this.guide);

        if (this.guide.user) {
          this.guide.createdBy =
            this.guide.user.name ||
            this.guide.user.username ||
            this.guide.user.email ||
            'Anónimo';
        } else {
          console.warn('No user data found for this guide:', this.guide);
          this.guide.createdBy = 'Anónimo';
        }
        console.log('Creador de la guía:', this.guide.createdBy);
      } catch (error) {
        this.errorMessage = error.response?.data?.message || 'Error al cargar la guía';
        console.error('Error al cargar la guía:', error);
      } finally {
        this.loading = false;
      }
    },
    handleImageError(event) {
      event.target.style.display = 'none';
      const placeholder = document.createElement('div');
      placeholder.className = 'no-image-placeholder';
      placeholder.textContent = 'Sin imagen disponible';
      event.target.parentNode.appendChild(placeholder);
    },
    goBackToGuides() {
      this.$router.push('/comunidad');
    },
    formatDate(dateString) {
      if (!dateString) return 'Fecha desconocida';
      const date = new Date(dateString);
      return date.toLocaleDateString('es-ES', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
      });
    },
    displayStars(rating) {
      const fullStars = Math.floor(rating);
      const emptyStars = 5 - fullStars;
      return '★'.repeat(fullStars) + '☆'.repeat(emptyStars);
    },
    async loadRatings() {
      try {
        const guideId = this.$route.params.id;
        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        await axios.get('sanctum/csrf-cookie');
        const response = await axios.get(`/api/guides/${guideId}/ratings`);
        this.ratings = response.data;
        this.calculateAverageRating();
      } catch (error) {
        console.error('Error al cargar las valoraciones:', error);
      }
    },
    calculateAverageRating() {
      if (this.ratings.length === 0) {
        this.averageRating = 0;
        return;
      }
      const total = this.ratings.reduce((sum, rating) => sum + rating.rating, 0);
      this.averageRating = total / this.ratings.length;
    },
    setUserRating(star) {
      this.userRating = star;
    },
    async submitRating() {
      if (this.userRating === 0 || !this.userReview.trim()) return;
      try {
        const guideId = this.$route.params.id;
        axios.defaults.baseURL = 'http://localhost:8000';
        axios.defaults.withCredentials = true;
        await axios.get('sanctum/csrf-cookie');
        const response = await axios.post(`/api/guides/${guideId}/ratings`, {
          rating: this.userRating,
          text: this.userReview,
        }, {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('auth_token')}`,
          },
        });
        this.ratings.push({
          id: response.data.id,
          author: this.guide.createdBy || 'Usuario',
          rating: this.userRating,
          text: this.userReview,
          created_at: new Date().toISOString(),
        });
        this.userRating = 0;
        this.userReview = '';
        this.calculateAverageRating();
        console.log('Valoración enviada:', response.data);
      } catch (error) {
        console.error('Error al enviar la valoración:', error);
        alert('Error al enviar la valoración. Por favor, intenta de nuevo.');
      }
    },
    toggleFavorite() {
      this.isFavorited = !this.isFavorited;
      console.log(`Guía ${this.isFavorited ? 'agregada a' : 'eliminada de'} favoritos`);
    },
    shareGuide() {
      const url = window.location.href;
      navigator.clipboard.writeText(url).then(() => {
        alert('Enlace copiado al portapapeles: ' + url);
      }).catch(err => {
        console.error('Error al copiar el enlace:', err);
        alert('No se pudo copiar el enlace. Por favor, copia manualmente: ' + url);
      });
    },
    loadComments() {
      // Simulación de carga de comentarios (puedes reemplazar con una llamada al backend)
    },
    submitComment() {
      if (!this.newComment.trim()) return;
      const newComment = {
        id: this.comments.length + 1,
        author: this.isAuthenticated ? (this.guide?.createdBy || 'Usuario') : 'Anónimo',
        text: this.newComment,
        created_at: new Date().toISOString(),
      };
      this.comments.push(newComment);
      this.newComment = '';
      console.log('Comentario enviado:', newComment);
    },
    editGuide() {
      this.$router.push(`/comunidad/crear-guia?editGuide=${this.guide.id}`);
    },
    async deleteGuide() {
      if (confirm('¿Estás seguro de que quieres eliminar esta guía?')) {
        try {
          const guideId = this.$route.params.id;
          axios.defaults.baseURL = 'http://localhost:8000';
          axios.defaults.withCredentials = true;
          await axios.get('sanctum/csrf-cookie');
          const response = await axios.delete(`/api/guides/${guideId}`, {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('auth_token')}`,
            },
          });
          console.log('Guía eliminada:', response.data);
          this.$router.push('/comunidad/guias'); // Redirigir a la lista de guías
        } catch (error) {
          this.errorMessage = error.response ? error.response.data.message : 'Error al eliminar la guía';
          console.error('Error al eliminar la guía:', error);
        }
      }
    },
    toggleDropdown(event) {
      const dropdown = event.currentTarget.parentElement.querySelector('.dropdown-menu');
      if (dropdown) {
        dropdown.style.display = dropdown.style.display === 'block' ? 'none' : 'block';
      }
    },
    closeDropdown(event) {
      const dropdowns = document.querySelectorAll('.dropdown-menu');
      dropdowns.forEach(dropdown => {
        if (!event.target.closest('.dropdown')) {
          dropdown.style.display = 'none';
        }
      });
    },
  },
};
</script>

<style scoped>
.guide-detail-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1e1e2f 0%, #2a2a3f 100%);
  display: flex;
  flex-direction: column;
  font-family: 'Arial', sans-serif;
  overflow-x: hidden; /* Evita el desplazamiento horizontal */
}

/* Barra de navegación */
.nav-container {
  width: 100%;
  padding: 15px 20px;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-link {
  text-decoration: none;
  font-size: 1.5em;
  font-weight: bold;
  color: #ff6f61;
  transition: color 0.3s;
}

.logo-link:hover {
  color: #e63946;
}

.nav-container ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}

.nav-container ul li {
  margin: 0 30px; /* Aumentamos el margen para un mejor espaciado */
}

.nav-container ul li a {
  text-decoration: none;
  color: #ff6f61;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  transition: color 0.3s, transform 0.3s;
}

.nav-container ul li a .icon {
  margin-right: 8px;
}

.nav-container ul li a:hover {
  color: #e63946;
  transform: scale(1.1);
}

.nav-container ul li a.active {
  color: #e63946;
  font-weight: bold;
  transform: scale(1.05);
}

/* Estilos para el dropdown (solo para Comunidad) */
.nav-container ul li.dropdown {
  position: relative;
}

.nav-container ul li.dropdown .dropdown-toggle {
  text-decoration: none;
  color: #ff6f61;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  transition: color 0.3s, transform 0.3s;
  cursor: pointer;
}

.nav-container ul li.dropdown .dropdown-toggle .icon {
  margin-right: 8px;
}

.nav-container ul li.dropdown .dropdown-toggle:hover {
  color: #e63946;
  transform: scale(1.1);
}

.nav-container ul li.dropdown .dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  min-width: 150px;
  z-index: 20;
  padding: 5px 0;
}

.nav-container ul li.dropdown:hover .dropdown-menu {
  display: block;
}

.nav-container ul li.dropdown .dropdown-menu li {
  display: block;
  margin: 0;
}

.nav-container ul li.dropdown .dropdown-menu li a {
  display: block;
  padding: 8px 15px;
  color: #ff6f61;
  text-decoration: none;
  font-size: 1em;
  transition: background 0.3s, color 0.3s;
}

.nav-container ul li.dropdown .dropdown-menu li a:hover {
  background: #ff6f61;
  color: white;
}

/* Contenido principal */
.guide-content-wrapper {
  max-width: 1200px;
  width: 100%; /* Aseguramos que no exceda el contenedor */
  margin: 20px auto;
  padding: 20px;
  background: #2a2a3f;
  border-radius: 10px;
  flex-grow: 1;
  box-sizing: border-box; /* Incluye padding en el ancho */
}

.loading-message,
.error-message {
  text-align: center;
  color: #ff6f61;
  font-size: 1.2em;
}

/* Encabezado de la guía */
.guide-header {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  margin-bottom: 20px;
}

.guide-image-container {
  width: 100px;
  padding-top: 100px;
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  background: #1e1e2f;
  flex-shrink: 0;
}

.guide-image-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 8px;
}

.no-image-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666;
  font-size: 0.9em;
  background: #1e1e2f;
}

.guide-title-container {
  flex-grow: 1;
  background: none;
  padding: 0;
  text-align: left;
}

.title-and-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.guide-title {
  color: white;
  font-size: 1.5em;
  margin: 0;
  text-transform: uppercase;
}

.header-actions {
  display: flex;
  gap: 10px;
}

.action-btn {
  padding: 8px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
  color: white;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background 0.3s, transform 0.3s;
}

.action-btn:hover {
  transform: scale(1.05);
}

.edit-btn {
  background: #ff6f61; /* Color similar al de "Agregar a Favoritos" */
}

.edit-btn:hover {
  background: #e63946;
}

.delete-btn {
  background: #f44336; /* Color similar al de "No me gusta" */
}

.delete-btn:hover {
  background: #da190b;
}

.guide-rating {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 5px 0;
}

.stars {
  color: #ccc;
  font-size: 1em;
}

.rating-text {
  color: #ccc;
  font-size: 0.9em;
}

.guide-author {
  color: #ff6f61;
  font-size: 1em;
  margin: 5px 0 0;
}

/* Botones de acción */
.guide-actions {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.like-btn {
  background: #4caf50;
}

.like-btn:hover {
  background: #45a049;
}

.dislike-btn {
  background: #f44336;
}

.dislike-btn:hover {
  background: #da190b;
}

.award-btn {
  background: #ff9800;
}

.award-btn:hover {
  background: #e68a00;
}

.favorite-btn {
  background: #ff6f61;
}

.favorite-btn:hover {
  background: #e63946;
}

.share-btn {
  background: #4285f4;
}

.share-btn:hover {
  background: #357abd;
}

/* Contenido principal y metadatos */
.guide-main-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 20px;
  width: 100%; /* Aseguramos que no exceda el contenedor */
  box-sizing: border-box; /* Incluye padding en el ancho */
}

.guide-body {
  background: #1e1e2f;
  padding: 20px;
  border-radius: 8px;
}

.guide-content-text {
  color: white;
  font-size: 1.1em;
  line-height: 1.6;
}

.guide-meta {
  background: #1e1e2f;
  padding: 20px;
  border-radius: 8px;
  color: white;
}

.guide-meta h3 {
  color: #ff6f61;
  font-size: 1.2em;
  margin-bottom: 15px;
}

.meta-item {
  margin-bottom: 10px;
}

.meta-label {
  font-weight: bold;
  color: #ff6f61;
}

.meta-value {
  color: white;
}
</style>