  min-height: 100vh;
  background: linear-gradient(135deg, #1e1e2f 0%, #2a2a3f 100%);
  display: flex;
  flex-direction: column;
  font-family: 'Arial', sans-serif;
  width: 100%;
  box-sizing: border-box;
}

/* Barra de navegación */
.nav-container {
  width: 100%;
  padding: 15px 20px;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  position: sticky;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}

.logo-link {
  text-decoration: none;
  font-size: 1.5em;
  font-weight: bold;
  color: #ff6f61;
  transition: color 0.3s;
}

.logo-link:hover {
  color: #e63946;
}

.nav-container ul {
  list-style: none;
  padding: 0 20px; /* Added padding to prevent links from touching the edge */
  margin: 0;
  display: flex;
  align-items: center;
}

.nav-container ul li {
  margin: 0 15px; /* Adjusted margin for more balanced spacing */
}

.nav-container ul li a,
.nav-container ul li button.logout-btn {
  text-decoration: none;
  color: #ff6f61;
  font-size: 1.2em;
  display: flex;
  align-items: center;
  transition: color 0.3s, transform 0.3s;
  background: none;
  border: none;
  cursor: pointer;
}

.nav-container ul li a .icon,
.nav-container ul li button.logout-btn .icon {
  margin-right: 8px;
}

.nav-container ul li a:hover,
.nav-container ul li button.logout-btn:hover {
  color: #e63946;
  transform: scale(1.1);
}

.nav-container ul li a.active {
  color: #e63946;
  font-weight: bold;
  transform: scale(1.05);
}

/* Contenedor principal */
.edit-profile-content {
  width: 100%; /* Changed to 100% to fit within the viewport */
  max-width: 1200px;
  margin: 30px auto;
  display: flex;
  gap: 20px;
  box-sizing: border-box;
  padding: 0 20px; /* Added padding to prevent content from touching the edges */
}

/* Sidebar */
.sidebar {
  width: 250px;
  background: rgba(255, 255, 255, 0.95);
  padding: 20px;
