  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
  box-sizing: border-box;
}

.section-title {
  margin: 0 0 10px 0;
  color: #ff6f61;
  font-size: 1.8em;
  animation: fadeIn 1.5s ease-in-out;
}

.section-description {
  color: #666;
  font-size: 1em;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
  text-align: left;
}

.form-group label {
  display: block;
  color: #666;
