  font-size: 1.2em;
  transition: transform 0.3s, background 0.3s;
}

.save-btn:hover {
  background: linear-gradient(90deg, #e63946, #ff9f1c);
  transform: scale(1.05);
}

.success-message {
  color: #2a9d8f;
  font-size: 1.1em;
  margin-top: 10px;
}

.error-message {
  color: #e63946;
  font-size: 1.1em;
  margin-top: 10px;
}

.loading-message {
  color: #ff6f61;
  font-size: 1.2em;
}

.back-link {
  margin-top: 20px;
  text-align: right;
}

.back-link a {
  color: #ff6f61;
  text-decoration: none;
  font-size: 1em;
}

.back-link a:hover {
  text-decoration: underline;
}

/* Footer */
.footer-section {
  background: linear-gradient(90deg, #2a9d8f, #1e1e2f);
  color: white;
  text-align: center;
  padding: 20px;
  width: 100%;
  margin-top: auto;
  box-sizing: border-box;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 900px;
  margin: 0 auto;
}

.social-links {
  display: flex;
  gap: 15px;
}

.social-icon {
  text-decoration: none;
  color: white;
  font-size: 1.5em;
  transition: color 0.3s;
}

.social-icon:hover {
  color: #ff6f61;
}

/* Animations */
@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Media Queries */
@media (max-width: 768px) {
  .edit-profile-content {
    flex-direction: column;
    padding: 0 10px; /* Reduced padding for smaller screens */
  }

  .sidebar {
    width: 100%;
  }

  .edit-profile-section {
    width: 100%;
  }

  .nav-container ul {
    padding: 0 10px; /* Reduced padding for smaller screens */
  }

  .nav-container ul li {
    margin: 0 10px; /* Reduced margin for smaller screens */
  }
