import { createRouter, createWebHistory } from 'vue-router';
import Game from '../components/Game.vue';
import MenuView from '../views/MenuView.vue';
import LoginView from '../views/LoginView.vue';
import PerfilView from '../views/PerfilView.vue';
import RegisterView from '../views/RegisterView.vue';
import CommunityView from '../views/CommunityView.vue';
import EditProfileView from '../views/EditProfileView.vue';
import GuideView from '../views/GuideView.vue';
import CreateGuideView from '../views/CreateGuideView.vue';


const routes = [
  { path: '/', component: MenuView, name: 'Home' },
  { path: '/login', component: LoginView, name: 'Login' },
  { path: '/register', component: RegisterView, name: 'Register' },
  { path: '/juego', component: Game, name: 'Juego' },
  { path: '/perfil', component: PerfilView, name: 'Perfil' },
  { path: '/perfil/editar', component: EditProfileView, name: 'EditProfile' },
  {
    path: '/comunidad',
    component: CommunityView,
    name: 'Comunidad',
    children: [
      {
        path: 'guias',
        name: 'Guias',
        component: CommunityView,
      },
    ],
  },
  { path: '/comunidad/guia/:id', name: 'GuideView', component: GuideView, props: true },
  { path: '/comunidad/crear-guia', name: 'CreateGuide', component: CreateGuideView, meta: { requiresAuth: true } },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/AboutView.vue'),
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('../views/NotFound.vue'),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;